<script>
   /*************************************************************/
   /* AppContainer for prediction                               */
   /* -------------------------                                 */
   /* app container for applying PCA model                      */
   /*************************************************************/

   import { downloadResults } from '../utils';

   import ModelLoader from './ModelLoader.svelte';
   import AppPlots from './AppPlots.svelte';

   import DataLoader from '../../../shared/components/PCADataLoader.svelte';
   import ResultTable from '../shared/ResultTable.svelte';

   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';


   /*****************************************/
   /* Component parameters                  */
   /*****************************************/

   // inputs
   export let WASM;              // WASM promis method from parent app


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // main properties of the training app
   // all set to undefined to avoid triggering
   // reset conditions on first render

   let dataset;               // new set
   let model;                 // object with PCA model
   let res;                   // object with PCA results for calibration set

   // parameters to check if model object has been updated
   let modelHash = 0;

   // array with outliers
   let outliers = [];         // vector with dataset row numbers (from 0) marked as outliers


   /**
    * Set the selected point as outlier and reset the model and results
    * @param e - event dispatched by AppPlots component with id of the selected point
    */
   function setOutlier(e) {

      // get position (index) of the sample to be removed
      const pos = parseInt(e.detail.id);
      if (Number.isNaN(pos) || pos < 0 || pos >= res.objindices.length ) return;

      // find sample ID corresponding to the position
      const id = res.objindices[pos];

      // filter the outlier out
      if (id >= 0 && !outliers.includes(id)) {
         outliers = [...outliers, id];
      }
   }


   /**
    * Reset outliers and model object
    */
   function resetOutliers() {
      outliers = [];
   }

   // if model is null - reset dataset
   $: if (model === null) {
      dataset = null;
      modelHash = 0;
   }


   // if dataset is null - reset all dependent objects (results, labels, outliers, etc.)
   $: if (dataset === null) {
      res = null;
      outliers = [];
   }

   // check hashes and initiate updates if outcomes changed
   $: if (model && model.hash !== modelHash) {
      modelHash = model.hash;
   }
</script>

<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="model-loader">
         <ModelLoader bind:model={model} />
      </div>

      {#if model}
      <div class="data-loader">
         <DataLoader bind:dataset={dataset} bind:res={res} {WASM} {model} {outliers} />
      </div>
      {/if}

      {#if outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers {dataset} on:resetoutliers={resetOutliers} bind:outliers={outliers} />
      </div>
      {/if}

      {#if res }
      <div class="model-dowloader">
         <Container label='Download'>
            <div class="buttons">
            <Button on:click={() => downloadResults(res, dataset, "new", outliers)}>Results</Button>
            </div>
         </Container>
      </div>
      <div class="result-stats">
         <ResultTable {res} />
      </div>
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !res}
         <div class="help-text">
            <p>
               In this tab you can project new data points to a previosly trained PCA model and explore main outcomes: scores, distances and variances.
            </p>
            {#if !model}
            <p><strong>Step 1.</strong></p>
            <p>Load file with PCA model you created earlier in tab <strong>Train</strong></p>
            {:else}
            <p><strong>Step 2.</strong></p>
            <p>Load your dataset set from a CSV file. The interface and selection steps are similar to the ones you used in <em>Train</em> tab. However, in this case you do not need to specify all details for header, just select if the new dataset has header as a first row or not. All values will be inherited from the model object.
            </p>
            <p><strong>It is important that the new dataset has same variables as the training set used to create the model.</strong></p>
            {/if}
            <p></p>
         </div>
      {:else}
         {#key res }
         <AppPlots on:setoutlier={setOutlier} {dataset} {res} {model} />
         {/key}
      {/if}
   </div>
</div>

