<script>
   /*************************************************************/
   /* Root component for an application                         */
   /*************************************************************/

   export let WASM;
   export let appTitle;
   export let appShortTitle;
   export let appSlogan;
   export let containers = [];
   export let isBeta = false;

   let selectedContainer = 0;

   let isInfoShown = false;
   function showInfo() {
      isInfoShown = true;
   }

   function closeInfo() {
      isInfoShown = false;
   }

   function selectContainer(id) {
      selectedContainer = id;
   }
</script>

<div class="page">

   <header class="page-header">
      <div class="header-group">
         <img class="logo" alt={appShortTitle} src="logo.svg" />
         <h1>{appTitle}{#if isBeta}<sup style="color:red">β</sup>{/if}</h1>
         <p>{appSlogan}</p>
      </div>
      <ul class="nav">
         {#each containers as container, i}
         <li class="nav-item" class:selected={selectedContainer === i} >
            <button on:click={() => selectContainer(i)} on:keydown={() => selectContainer(i)}>{container.name}</button>
         </li>
         {/each}
      </ul>
   </header>

   <main>
      {#each containers as container, i}
      <div class:hidden={selectedContainer !== i} class="app-container">
      <svelte:component this={container.container} {WASM} />
      </div>
      {/each}
   </main>

   <footer>
      <button class="button-info" on:click={showInfo} on:keydown={showInfo}>info</button>
      <slot name="footer">
      </slot>
   </footer>

   {#if isInfoShown}
   <div class="info-backdrop">
      <div class="info-container">
         <button class="info-close-button" on:click={closeInfo}>✕</button>
         <slot>
         </slot>
      </div>
   </div>
   {/if}
</div>

