import { Matrix } from 'mdatools/arrays';
import { pca_predict, pcamodel_get } from './wasm/dist/pca.js';


// constant values (colors, etc)
export const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'target': '#2679B2', 'alt': '#2679B2'};
export const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#fff8f0', 'target': '#f0f8ff', 'alt': '#f0f8ff'};
export const LABELS_COLOR = '#a0a0a0';
export const LABELS_COLOR_SELECTED = '#909090';


/**
 * Creates arrays with object indices, labels and classes without outliers
 * @param {Object} dataset — JSON with dataset object.
 * @param {Array} outliers — array with object indices removed as outliers.
 * @returns {Array} array with three arrays — object indices, classes and labels
 */
function handle_outliers(dataset, outliers) {
   let indices, classes, labels;
   if (outliers.length > 0) {
      // filter the outlier out
      const nAll = dataset.values.nrows;
      const sampleIndices = dataset.objindices;
      const n = nAll - outliers.length;
      indices = Array(n);
      classes = Array(n);
      labels = Array(n);
      let j = 0;
      for (let i = 0; i < nAll; i++) {
         if (outliers.includes(i)) continue;
         indices[j] = sampleIndices[i];
         classes[j] = dataset.classes[i];
         labels[j] = dataset.objlabels[i]
         j = j + 1;
      }
   } else {
      indices = dataset.objindices;
      labels = dataset.objlabels;
      classes = dataset.classes;
   }

   return [indices, labels, classes];
}


/**
 * Generates unique pseudo-hash number based on current time and date.
 * @returns  string with the hash
 */
export function genhash() {
    const now = new Date();
    const uniqueId = `${now.getFullYear()}${(now.getMonth() + 1)
        .toString()
        .padStart(2, '0')}${now.getDate().toString().padStart(2, '0')}${now
        .getHours()
        .toString()
        .padStart(2, '0')}${now.getMinutes().toString().padStart(2, '0')}${now
        .getSeconds()
        .toString()
        .padStart(2, '0')}${now
        .getMilliseconds()
        .toString()
        .padStart(3, '0')}`;
    return uniqueId;
}


/**
 * Applies PCA model to a new dataset and adds additional attributes.
 *
 * @param {Promise} Module - WASM module (from Promise).
 * @param {Object} model - object with PCA model.
 * @param {Matrix} dataset - object with original dataset.
 * @param {Array} outliers - array with outliers.
 *
 * @returns {Object} object with PCA results
 */
export function app_pca_predict(Module, model, dataset, outliers) {

   // get arrays with sample indices, classeas and object labels without outliers
   const [objindices, objlabels, classes] = handle_outliers(dataset, outliers);

   // make predictions and add labels and indices
   const r = pca_predict(Module, model, dataset.values, outliers);
   r['hash'] = genhash();
   r['objindices'] = objindices;
   r['objlabels'] = objlabels;
   r['classes'] = classes;
   r['classNames'] = dataset.classNames;
   r['varlabels'] = dataset.varlabels;

   // model outcomes required for plots
   r['eigenvals'] = pcamodel_get(model, 'eigenvals');
   r['cNh'] = pcamodel_get(model, "cNh");
   r['cNq'] = pcamodel_get(model, "cNq");
   r['ch0'] = pcamodel_get(model, "ch0");
   r['cq0'] = pcamodel_get(model, "cq0");
   r['rNh'] = pcamodel_get(model, "rNh");
   r['rNq'] = pcamodel_get(model, "rNq");
   r['rh0'] = pcamodel_get(model, "rh0");
   r['rq0'] = pcamodel_get(model, "rq0");

   return r;
}


/**
 * Parse string with data from a CSV file and create a matrix with values.
 *
 * @param {string} strData - string with all data from the CSV file.
 * @param {string} sep - symbols used for separation of values (by default ',').
 * @param {string} header - type of header (first row in the data).
 * @param {boolean} hasLabels - logical, if first column must be treated as object labels or not.
 * @param {boolean} hasClasses - logical, if column next to labels must be treated as class names or not.
 * @param {string} varvaluesName - name for variable values (e.g. Wavelength)
 * @param {string} varvaluesUnits - units for variable values (e.g. nm)
 *
 * @returns {Object} JSON containing matrix with parsed values, header, values and decimal separators.
 */
export function parseCSV(strData, sep, header, hasLabels, hasClassNames, varvaluesName, varvaluesUnits) {


   // function which returns true if at least one value in array can not be parsed to float
   function hasNaN(vec) {
      return vec.some(v => Number.isNaN(Number.parseFloat(v.trim())));
   }

   // set default separator symbol
   if (sep === undefined) {
      sep = ",";
   }

   if (!(sep === ',' || sep === ';')) {
      throw new Error('Values separator can be either comma or semicolon.')
   }
   // setup decimal separator
   const dec = sep === ";" ? "," : ".";

   // remove BOM symbol if any
   strData = strData.replace(/^\uFEFF/gm, "").replace(/^\u00EF?\u00BB\u00BF/gm,"");

   // remove \r symbols
   strData = strData.replace(/\r/gm, "");

   // split string with data values into array of rows and filter empty rows
   let rows = strData.split(/\n/).filter(v => v.trim().length > 1);
   if (rows.length < 1) {
      throw new Error('Could not find non-empty rows with values, check your CSV file and try again.');
   }

   // get array with values from the first row
   const firstRowValues = rows[0].split(sep);

   // check if the first row contains header
   let startRow = 0;
   let headerLabels;
   const hasHeader = header !== 'no';
   if (hasHeader || (hasHeader === undefined && hasNaN(firstRow))) {
      headerLabels = firstRowValues;
      startRow = 1;
   }


   // number of rows and columns in future matrix
   const nrows = rows.length - startRow;
   if (nrows < 1) {
      throw new Error("Data file should contain at least 1 row with values.");
   }


   // check if first column contains row labels
   let startColumn;
   let objlabels;
   let objindices = Array(nrows);
   if (hasLabels) {
      objlabels = Array(nrows);
      startColumn = 1;
   } else{
      objlabels = [];
      startColumn = 0;
   }

   // check if next column contains class names
   let classNames = [];
   let classes;
   if (hasClassNames) {
      classes = Array(nrows);
      startColumn = startColumn + 1;
   } else {
      classes = [];
   }

   // compute number of columns
   const ncols = firstRowValues.length - startColumn;
   if (ncols < 1) {
      throw new Error("Data file should contain at least 1 column with values.");
   }

   // set rows of data as columns of matrix - it is faster
   const data = Matrix.zeros(ncols, nrows);

   // find symbol used for decimals separator
   for (let r = startRow; r < nrows + startRow; r++) {

      // add row/object index
      const rind = r - startRow;
      objindices[rind] = rind;

      // split the row string into array of values
      let rowValuesAll = rows[r].split(sep)

      // if the first element is row label add it to labels array and increase the offset
      if (hasLabels) {
         objlabels[rind] = rowValuesAll[0];
         rowValuesAll.shift()
      }

      // if the next element is class/group label add it to classes array and increase the offset
      if (hasClassNames) {
         const className = rowValuesAll[0];
         classes[rind] = className;
         rowValuesAll.shift()

         // also add it to array of unique class names
         if (classNames.length === 0 || classNames.indexOf(className) < 0) {
            classNames.push(className);
         }
      }

      // for the rest of values, trim spaces, replace separator symbol with dot (.) and convert to numbers
      const rowValues = dec === '.' ?
         rowValuesAll.map(v => Number.parseFloat(v.trim())) :
         rowValuesAll.map(v => Number.parseFloat(v.trim().replace(dec, ".")));

      if (rowValues.some(v => Number.isNaN(v))) {
         throw new Error("Some of the values can not be converted to numbers, check your CSV file and try again.");
      }

      if (rowValues.length != ncols) {
         throw new Error("Wrong number of values in row #" + r);
      }

      data.v.set(new Float64Array(rowValues), (r - startRow) * ncols);
   }

   // generate row labels if they are not provided
   if (!hasLabels || objlabels.length !== nrows) {
      objlabels = Array(nrows);
      for (let i = 0; i < nrows; i++) {
         objlabels[i] = 'O' + (i + 1);
      }
   }

   function clearLabels(s) {
      return s.replace('\t', '').trim().replace(/^(['"])(.*)\1$/, '$2');
   }

   // remove possible extra quotes around the labels and classes
   objlabels = objlabels.map(v => clearLabels(v));
   classes = classes.map(v => clearLabels(v));
   classNames = classNames.map(v => clearLabels(v));

   // handle variable labels, indices and values
   let varlabels;
   let varindices = Array(ncols);
   if (headerLabels && headerLabels.length === ncols + startColumn) {
      varlabels = headerLabels.map(v => clearLabels(v)).slice(startColumn);
      for (let i = 0; i < ncols; i++) {
         varindices[i] = i;
      }
   } else {
      varlabels = Array(ncols);
      for (let i = 0; i < ncols; i++) {
         varlabels[i] = 'V' + (i + 1);
         varindices[i] = i;
      }
   }

   // parse variable labels as numeric values if it is expected
   let varvalues;
   if (header === 'values') {
      varvalues = dec === '.' ?
         varlabels.map(v => Number.parseFloat(v.trim())) :
         varlabels.map(v => Number.parseFloat(v.trim().replace(dec, ".")));
      if (varvalues.some(v => Number.isNaN(v))) {
         throw new Error("Some of the header values can not be converted to numbers, check your CSV file and try again.");
      }
   } else {
      varvalues = [];
   }

   return {
      values: data.t(),
      header,
      objlabels,
      objindices,
      varlabels,
      varvalues,
      varindices,
      classes,
      classNames,
      sep,
      dec,
      varvaluesName: varvalues.length > 0 && varvaluesName.length > 0 ? varvaluesName : 'Variables',
      varvaluesUnits: varvalues.length > 0 && varvaluesUnits.length > 0 ? varvaluesUnits : ''
   };
}
